@import "sass/index";

.paper {
  width: 470px;
  height: max-content;
  border-radius: 10px;
  background: linear-gradient(
      135deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 48.48%,
      rgba(3, 1, 4, 0.0001) 80.79%,
      #000000 100%
    ),
    #0a0d18;
  @media (max-width: 500px) {
    width: 340px;
  }
}
.box {
  padding: spacing(1.5);
  border-radius: spacing(1);
  margin: 10px;
}
.titleWrapper {
  margin: 20px 20px 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 22px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
}
.closeIcon {
  width: 24px;
  height: 24px;
}

.block {
  min-width: 100%;
  width: 100%;
  margin: 0;
  display: grid;
  grid-template-columns: 75% 25%;
  gap: 10;

  @media (max-width: 400px) {
    grid-template-columns: 65% 35%;
  }
}
.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  & > :first-child {
    margin-top: 0px !important;
  }
}
.inputWrapper {
  position: relative;
  width: 100%;
}
.input {
  margin: 0;
  height: 60px;
  font-size: 18px;
  color: #ffffff;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(128, 66, 225, 0.35);
  border-radius: 4px;
}
.select {
  position: absolute;
  right: 10px;
  width: 64px;
  top: 50%;
  min-width: 64px !important;
  transform: translateY(-50%);
  border-radius: 6px;
  background-color: transparent;
  border: 1px rgba(128, 66, 225, 0.35) solid;

  align-self: center;

  &Label {
    display: none;
  }

  &Icon {
    width: 24px;
    height: 24px;
  }

  &Item {
    padding-top: spacing(1);
    padding-bottom: spacing(1);
    padding-left: spacing(2);
    padding-right: spacing(2);
    & > span {
      font-family: "Gilroy";
      font-size: 18px;
    }
    & > img {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }
  }

  &OptionsContainer {
    padding-top: 6px;
    padding-bottom: 6px;
    background-color: white;
  }

  &ExpandIcon {
    transform: scale(0.6);
  }
}
.rootSelect {
  padding: 7px 10px 7px 10px;
}
.button {
  margin-top: 15px;
  background: transparent;
  border: 1px solid #4835ea;
  border-radius: 8px;
  color: #ffffff;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 1px;
  padding: 10px 66px;

  &:hover {
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    );
  }
}
.inputs {
  width: 100%;
  position: relative;
  display: flex;
}
.inputIcon {
  width: 26px;
  height: 26px;
  position: static;
}
.buttonWrapper {
  display: flex;
  justify-content: center;
}
